<template>
  <el-row>
    <el-button type='primary' @click='changeScanState'>{{ buttonText }}</el-button>
    <el-button type='primary' @click='stopScan'>结束扫描</el-button>
    <i :class='connectStatus' id='connection'></i>
    <textarea v-model='logInfoText' id='logInfo' readonly></textarea>
  </el-row>
</template>
<style>
  #logInfo{
    border: 0;
    padding: 0;
    float: right;
    resize: none;
    width: 50%;
    outline: none
  }
  #connection{
    margin-left: 1%
  }
</style>
<script>
  import io from 'socket.io-client'

  export default {
    data () {
      return {
        connectStatus: 'el-icon-circle-close',
        buttonText: '开始',
        logInfoText: '扫描状态'
      }
    },
    mounted () {
      let socket = io('http://localhost:5000')
      socket.emit('init', '123')

      socket.on('connect', () => {
        this.$store.commit('connectSuccess')
      })
      socket.on('connect_error', () => {
        this.$store.commit('connectFail')
      })
      socket.on('disconnect', () => {
        this.$confirm('与后台连接已断开，是否重新连接？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          socket.socket.connect()
          this.$store.commit('startScan')
          this.$message.success('扫描继续！')
        }).catch(() => {
          this.$message.info('已断开连接')
          socket.disconnect()
          this.buttonText = '继续'
          this.$store.commit('connectFail')
        })
      })
      socket.on('reportNext', (data) => {
        if (data) {
          this.$store.commit('addDev', JSON.parse(data))
        }
        if (this.$store.state.runStatus === 'Running') {
          this.socket.emit('scanNext')
        }
      })
      socket.on('logInfo', (data) => {
        this.logInfoText += data
        let logInfo = document.getElementById('logInfo')
        logInfo.scrollTop = logInfo.scrollHeight
      })

      this.socket = socket
    },
    methods: {
      changeScanState () {
        if (!this.$store.state.isConnected) {
          this.$message.error('失败！连接断开')
        } else if (this.$store.state.runStatus === 'Running') {
          this.$store.commit('pauseScan')
          this.$message.success('扫描暂停！')
        } else {
          this.$store.commit('startScan')
          this.$message.success('扫描继续！')
        }
      },
      stopScan () {
        if (!this.$store.state.isConnected) {
          this.$message.error('失败！连接断开')
        } else {
          this.$store.commit('stopScan')
          this.$message.success('扫描已终止！')
        }
      }
    },
    watch: {
      '$store.state.isConnected': {
        handler (status) {
          this.connectStatus = status ? 'el-icon-circle-check' : 'el-icon-circle-close'
          status ? this.$message.success('连接成功') : this.$message.error('连接断开')
        }
      },
      '$store.state.runStatus': {
        handler (status) {
          switch (status) {
            case 'Running':
              this.buttonText = '暂停'
              this.socket.emit('scanNext')
              break
            case 'Paused':
              this.buttonText = '继续'
              break
            case 'Stopped':
              this.buttonText = '开始'
              this.socket.emit('clear')
              break
          }
        }
      }
    }
  }
</script>



